var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"reader"},[_c('h2',{staticClass:"title-post-page"},[_vm._v("ANNA KARENINA")]),_c('div',{staticClass:"post-container"},[_c('h3',{staticClass:"post-title"},[_vm._v("L’immagine dell’eroina ribelle e dell’individuo in crisi: Anna e Levin come riflessi del cominciamento della crisi spirituale di Tolstoj")]),_c('p',{staticClass:"post-body"},[_vm._v(" La letteratura russa offre una straordinaria galleria di romanzi tra loro così profondamente diversi e talmente accattivanti e seducenti, che risulta veramente difficile operare una scelta che riproduca almeno in minima parte la ricca e variegata interpretazione della realtà femminile che questi stessi dispiegano al loro interno, e l’operazione si complica maggiormente qualora ci si debba orientare tra una gamma ristretta di opere. Il criterio fondamentale dal quale mi sono fatta guidare nella scelta è stato innanzitutto quello di rintracciare, tra le varie opere che Lei ci ha proposto, quella che rifletteva complessivamente la realtà femminile verso il tramonto del XIX secolo in Russia, ma che allo stesso tempo testimoniava la grandezza della letteratura russa nel panorama letterario mondiale. Anna Karenina non è semplicemente un libro, è un libro pieno di libri, ognuno dei quali racconta la storia di una donna: madre, amante, sposa ella è una costante imprescindibile del capolavoro di Lev Tolstoj, il quale ci offre una ricca collezione di ritratti femminili all’interno di un quadro sociale e culturale, ormai secolare, nel quale la donna si deve riconoscere nel ruolo esclusivo di madre buona e moglie fedele. Ogni tentativo di mettere a fuoco il profilo di questi personaggi deve sempre obbligatoriamente passare attraverso la presa di coscienza di due caratteristiche, tra di loro fortemente intrecciate, che informano l’iconografia femminile già dagli albori della civiltà russa e che sopravvivono a cambiamenti storici e sociali enormi, giungendo pressoché incolumi sino agli anni Settanta del 1800. La prima riguarda la natura stessa della società russa, la quale è intrisa di una mentalità misogina, che vede al suo comando il “pater”, padre di famiglia, uomo di fede, cittadino modello, esempio per la comunità, al cui vertice si colloca lo zar. La seconda, inevitabile derivazione della prima, è il “mito della madre” attraverso il quale la maternità viene tradizionalmente concepita come dovere supremo di ogni donna e come apoteosi della sua stessa identità. Questo mito antichissimo (è in epoca arcaica, infatti, che si costituisce il mito della madre come metafora della patria e della terra natia da cui le espressioni mat’ syraja zemlja - madre umida terra e rodina mat’- terra natia), depositato ormai nella coscienza sociale, guarda dunque la maternità come ad un momento di felicità idilliaca e di piena realizzazione per la donna. Helena Goscilo, titolare della cattedra di Lingue e Letterature Slave all’Università di Pittsburg, nel suo libro Dehexing Sex: Russian Womanhood during and after Glasnost (Disfare la Maledizione del Sesso: La Femminilità Russa durante e dopo la Glasnost’) lamenta il fatto che la rigidità del codice patriarcale, che valuta la donna solo se madre, si rifletterebbe anche sulla rigidità dei modelli letterari, i quali si limitano a due ruoli esclusivi: da un lato sta l’ideale della donna russa tradizionale, custode di valori come la famiglia, protettrice del focolare domestico, moglie docile, madre generosa; dall’altro sta l’eroina ribelle, risoluta contestatrice dei valori tradizionali, insofferente ai ruoli canonici di madre e moglie, dotata di pathos; questo dualismo di ruoli è rappresentato in maniera paradigmatica in Anna Karenina. Madre perennemente impegnata e premurosa, Dar’ja Aleksandrovna Ščerbatskaja (per gli intimi Dolly) è l’archetipo della donna russa tradizionale che sopporta in silenzio le infedeltà del marito Stepan Arkad’evič Oblonskij, ama con dedizione i propri figli e sacrifica completamente se stessa per la famiglia, per quell’uomo che nonostante tutto continua ad amare perché lui è suo marito, padre dei suoi cinque figli; questo si può facilmente evincere da una descrizione di lei che leggiamo all’inizio del cap. IV della PARTE PRIMA: “Era intenta a fare quello che tentava di fare per la decima volta: raccogliere le sue cose e dei bambini da portare a casa di sua madre, ma ancora una volta non riusciva a decidersi; e anche questa volta, come in precedenza, si diceva che le cose non potevano restare così, che doveva fare qualcosa, punirlo, svergognarlo, vendicarsi almeno di una piccola parte del dolore che lui le aveva causato. Si diceva ancora che lo avrebbe lasciato, ma sentiva che non era possibile; era impossibile perché non sarebbe riuscita a disabituarsi al fatto che lui era suo marito e ad amarlo.” “Ieri sera mi ha detto di aver immaginato un tipo di donna sposata dell’alta società che si è perduta”, questo è ciò che annotava nel suo diario all’anno 1870 la moglie di Tolstoj, Sof’ja Andreevna Bers. In questa breve annotazione c’è già in nuce l’immagine dell’adultera Anna Arkad’evna Karenina, la quale Tolstoj rielaborò ed approfondì nelle dodici diverse redazioni del romanzo, frutto di infinite revisioni e correzioni. Anna, maritata ad Aleksej Aleksandrovič Karenin, burocrate dello Stato, dal quale ha un figlio, Serëža, si perde quando la bruciante passione per l’ufficiale Aleksej Vronskij la attraversa e tutto il mondo in cui ha sempre vissuto crolla rovinosamente. Incapace di vivere la sua storia d’amore in clandestinità, come invece fa la maggior parte delle dame dell’alta società del suo tempo, rende pubblica la sua relazione extraconiugale scontrandosi così con l’ipocrisia e la frivolezza di una classe sociale molto spesso dominata da una morale bigotta e dispotica, che annichilisce la libertà individuale e sopprime il desiderio del singolo. Anna, però, si perde soprattutto perché è schiacciata dal peso della sua colpa, ribellandosi non regge la tensione della lotta e finisce miseramente, rivelandosi lei stessa schiava dei pregiudizi dell’alta società. Ella sacrifica ogni cosa all’amore, la posizione sociale, il buon nome e perfino l’affetto dell’amatissimo figlio, gettandosi furiosamente tra le braccia dell’amante Vronskij ed illudendosi così di vivere quell’amore totalizzante ed appassionato che aveva sognato per tutta la vita e che non aveva mai trovato nel marito, perennemente alle prese con gli affari di Stato. Continua ingenuamente ad illudersi anche quando, in seguito alla confessione a Karenin dell’avvenuto tradimento, fugge con l’amante e pensa di essersi liberata definitivamente delle convenzioni sociali, però, una volta tornata a Pietroburgo, sottoposta all’ostracismo della società, capisce di essersi tragicamente e completamente sbagliata; è impossibile liberarsi della società e Vronskij si rivela essere simile allo stesso uomo conformista dal quale era scappata, ovvero l’arido marito Karenin, e non l’amante ideale quale si era prefigurato. Di fronte all’amara disillusione, Anna palesa la sua fragilità e la sua confusione, non trova la forza di continuare a lottare ma non ha nemmeno il coraggio di pentirsi, né la determinazione di ricostruire la propria lacerata identità, e sceglie il suicidio come unica possibile via di fuga. “Laggiù! – si diceva osservando l’ombra del vagone, la polvere mista a carbone che ricopriva le traverse, - laggiù, proprio nel mezzo, così lo punirò e mi libererò di tutti e di me stessa (…) cadde sotto il treno riversa sulle braccia e, con un leggero movimento, come si apprestasse a rialzarsi subito, si inginocchiò (…) Avrebbe voluto rialzarsi, tirarsi fuori di lì; ma qualcosa di enorme, di inesorabile le colpì la testa e la trascinò via sulla schiena (…) E la candela alla cui luce aveva letto un libro pieno di angosce, di inganni, di dolore e di malvagità, avvampò di una luce più chiara che mai, le rischiarò tutto ciò che prima era oscuro, crepitò, si offuscò e si spense per sempre.” Con queste parole Tolstoj decide di porre fine alla storia della sua eroina, nello stesso luogo dove era iniziata, in una stazione; il treno, mezzo di trasporto sul quale per la prima volta Vronskij fu colpito dalla schiettezza e dalla delicatezza di Anna, testimone di un amore adultero e peccaminoso, la trascina ora senza pietà e con la stessa forza con la quale l’Amore aveva trascinato Anna nella “selva oscura” togliendola dalla via del bene. Nella sua tragica esperienza si riflette, però, una vicenda archetipica e sempre attuale di un sogno ingenuo e fanciullesco che resta inevitabilmente e drammaticamente deluso. La storia di un’anima femminile che sogna, si culla nelle illusioni, vive romanticamente l’amore come esperienza totalizzante ed assoluta. Anna è una donna senza tempo, è tanto primitiva nella forza istintiva e travolgente delle sue passioni quanto straordinariamente moderna in quanto esprime una mentalità femminile eterna, quella dei cuori ingenui che rifuggono la realtà consacrandosi all’amore. Anna Karenina si pone, oltre che come antitesi della donna russa tradizionale, anche come un paradigma insuperabile di femminilità e di bellezza. Un momento importante da questo punto di vista è il ritratto di Anna eseguito da Michàjlov in Italia: il ritratto colpì tutti, e in particolare Vronskij, non soltanto per la somiglianza, ma anche per una particolare bellezza. Era strano come Michàjlov avesse potuto cogliere la particolare bellezza di Anna. “Bisognava conoscerla e amarla come l’ho amata io, per trovare quella sua espressione spirituale così cara”, pensava Vronskij, benché soltanto da questo ritratto egli avesse cominciato a conoscere quella cara espressione spirituale di lei. L’episodio è assai significativo, poiché lascia emergere tutta la superficialità dell’amore di Vronskij, visto che un perfetto sconosciuto quale è il pittore riesce a leggere nell’anima di Anna più a fondo di quanto non sia mai stato capace di fare egli stesso. Ella ci viene presentata come una creatura luminosa, affascinante per la sua grazia spontanea e la sua disperata voglia di vivere, ma a questa immagine Tolstoj pervenne in maniera molto travagliata, il romanzo infatti passò attraverso una dozzina di stesure congruentemente con il suo modo di lavorare; scriveva infatti a Strachov il 13 Febbraio 1874 “Io non sono capace di tracciare un cerchio in altro modo che chiudendolo prima e poi correggendo le imprecisioni iniziali. Così adesso ho appena chiuso il cerchio e correggo, correggo.” Il primo abbozzo del romanzo, senza titolo, conteneva la storia di una relazione extraconiugale tra Tat’jana Stavrovič, la futura Anna, ed Ivan Balašov (che poi sarà Vronskij), che è nota a tutti tranne che al marito; la prima scena, infatti, si apre in medias res ed è ambientata nel salotto di una grande dama (la futura principessa Tverskaja), gli ospiti della quale sono animati da una tipica conversazione mondana costruita attorno alla scandalosa relazione tra Tat’jana ed Ivan, non mancando a questo proposito velenosi e futili pettegolezzi. Tat’jana è una figura mediocre e volgare, non bella ma sensuale, ancora lontana dalla figura delicata ed elegante che sarebbe diventata in seguito Anna, ma è interessante notare come in essa si possano già scorgere alcuni tratti del futuro profilo di Anna quali “l’allegra, squillante risata”, “i grandi occhi neri”, “i modi sorprendentemente franchi”. Un elemento essenziale della storia di Tat’jana sembra degno di nota: ella, a differenza di Anna, divorzia dal marito dopo aver avuto un figlio dall’amante Ivan, che sposerà successivamente, ma la storia finisce comunque tragicamente con lei che si butta nel fiume Neva (ricorda da questo punto di vista la Bednaja Liza di Karamzin). Dunque, questo mi pare un punto fondamentale ai fini di quella che poi sarà la stessa tragica fine di Anna: la soluzione non risiede nel matrimonio, il quale è ancora una volta un atto di conformismo e di inquadramento in una società gretta ed immobile, che non si discosta dalle proprio convinzioni bigotte. Il matrimonio non porta ai due né serenità né felicità e la loro relazione continua ad essere guardata con ostilità nell’alta società, della quale sono membri, e l’ossessiva gelosia di lei finirà con l’incrinare sempre più il loro rapporto. In questa prospettiva, Anna impara la lezione di Tat’jana e si sviluppa nell’eroina ribelle, la quale comprende che il matrimonio è una tra quelle regole prescritte dal codice sociale al quale si vuole opporre e la sua ostinazione di fronte all’ipotesi del divorzio non è altro che la conferma delle sue convinzioni. Di fronte alle ripetute proposte di Vronskij di chiedere il divorzio a Karenin, Anna nega ogni possibilità, consapevole che un matrimonio costruito sulle fondamenta di un adulterio non servirebbe a riabilitare la sua posizione sociale e nemmeno a guarirla da quell’immagine di peccatrice che si rifletteva su di lei ogni volta che si guardava allo specchio ingannevole della società. Anna, però, è anche l’eroina tragica che non riesce a portare a termine la sua battaglia poiché la forza motrice, quella che le dava la forza per lottare, ovvero l’amore per Vronskij, si rivela fiacca; trasformatosi in virtù quotidiana il sentimento smette per ella di essere autentico il che comporta la fine dell’Eros. Con la seconda redazione si ha già un approfondimento della dimensione psicologica e spirituale dei personaggi, i quali sono sempre più simili alla versione finale, ivi compare infatti anche per la prima volta il nome Karenin in relazione al marito dell’adultera. Questo secondo abbozzo vede protagonisti Anastas’ja e Udašev, quest’ultimo avendo già delineate alcune delle caratteristiche di Vronskij, come per esempio “i denti integri, forti, disposti in semicerchio regolare” ed il “volto risoluto, espressivo”. In Anastas’ja, invece, si crea un dualismo tra la sua bellezza interiore e la bruttezza esteriore, la prima prevalendo sulla seconda, e soprattutto di delinea il contrasto tra l’eroina e la società, un tema che poi risulterà decisivo nella versione finale. Il personaggio di Anna, splendida e seducente, assorbe in sé la provocante Tat’jana e la bellezza interiore di Anastas’ja, risultando così un’eroina complessa e stratificata, in ella vi è una confusa mescolanza degli opposti che poi è l’essenza stessa della realtà. Una realtà la quale, com’è facilmente deducibile, viene filtrata attraverso lo sguardo di Tolstoj stesso. Tolstoj l’uomo alla perenne ricerca della verità che è interna all’uomo e si può conoscere solo penetrando l’essenza dell’uomo, nel suo concreto esistere; Tolstoj con i suoi legami coniugali e familiari, che si fanno via via più stretti e difficili; Tolstoj con i suoi dubbi tormentosi e le continue domande sull’esistenza umana, ma soprattutto con la sua profonda ed angosciosa crisi spirituale. Nella Confessione, trattato autobiografico scritto tra il 1879 ed il 1882, Tolstoj descrive nel dettaglio questo processo interiore: “E così appunto io vivevo, ma cinque anni or sono (dal 1874) cominciò ad accadermi qualcosa di molto strano: mi capitava dapprima di avere dei momenti di perplessità, come se la vita si arrestasse e io non sapessi più come vivere né cosa fare, e così mi smarrivo e cadevo in preda allo sconforto. Ma poi tutto questo passò ed io ripresi l’esistenza di prima. In seguito però questi momenti di perplessità presero a ripetersi sempre più di frequente, e sempre nella stessa forma. Questi arresti della vita si manifestavano sempre sotto l’aspetto delle medesime domande: Perché? E poi? (...) Erano domande in apparenza così sciocche, così semplici, addirittura infantili. Ma non appena tentai di rispondervi mi resi subito conto che, in primo luogo, non si trattava affatto di domande sciocche ed infantili, bensì di quesiti più profondi, più decisivi della vita e, in secondo luogo, che non ero assolutamente in grado di rispondervi, per quanto mi ci rompessi il capo. (…) La mia vita si era arrestata. Potevo respirare, mangiare, bere, dormire, ed anzi non potevo fare a meno di respirare, mangiare, bere e dormire, ma la vita non c’era più.” Il dissidio interiore del quale parla Tolstoj si può già intravvedere in lui nel periodo successivo alla conclusione di Guerra e Pace (Vojna i mir), avvenuta nel 1869, infatti i due anni seguenti furono caratterizzati da apatica malinconia e sfinimento intellettuale, due anni di silenzio letterario segnati da progetti mai portati a termine, ad exemplum il romanzo su Pietro il Grande, o addirittura mai iniziati. Nel 1871 comincia a lavorare alla compilazione di un Abbecedario per le scuole elementari, che esce l’anno successivo e vende più di un milione di copie, sebbene stroncato dalla critica come opera inutile. L’anno 1872 è un anno importante anche perché viene a conoscenza di un fatto quotidiano avvenuto il 4 gennaio quando una donna di nome Anna Stepanovna Pirogova muore suicida nella stazione di Jasenki della ferrovia Mosca-Kursk. La donna che si era perduta, della quale aveva parlato alla moglie nel 1870 e che era stata un’immagine apparsagli mentre lottava con la sonnolenza, trova così anche la sua tragica fine; Anna si suicida in una stazione esattamente come l’omonima della cronaca. A tutto questo si aggiunge la lettura casuale, avvenuta una mattina del marzo 1873, di un frammento di Puškin, Gli ospiti arrivavano alla villa della contessa G., del quale lo seduce il modo diretto di entrare immediatamente nel racconto, l’agilità della prosa puškiniana dissipa i suoi dubbi artistici e scorge finalmente il modo per dare corpo ad una storia che già stava maturando in lui da tempo, Anna Karenina. Il romanzo comparve a puntate sul Messaggero Russo tra il 1875 ed il 1877. Il tema si configurava come attualissimo: la questione femminile occupava uno spazio rilevante nella saggistica europea del tempo ed il dibattito culturale coinvolgeva molti scrittori e letterati. A tal proposito è interessante notare come le tre adultere più affascinanti della storia furono concepite proprio in questo periodo: difatti nel 1857 era uscito Madame Bovary di Flaubert, nel 1875/77 Anna Karenina, e infine, nel 1895 uscirà Effi Briest di Fontane. Tutte e tre sono l’emblema della trasgressione sessuale e rappresentano in modi diversi la perdizione della donna che di fronte alla sua caduta non trova altro rimedio fuorché la morte. La cosa interessante di Anna Karenina è notare che, sebbene inizialmente fosse stato concepito come romanzo psicologico su una donna perduta, in fine risultò un’opera ben più ampia ed articolata che registra i profondi cambiamenti sociali, politici e culturali avvenuti in Russia tra gli anni Sessanta e Settanta dell’Ottocento. Un’indagine sociale portata avanti da un uomo di saldi principi morali il quale osa porre domande che riguardano principi fondamentali della vita con la stessa tensione, la stessa sincerità e lo stesso coraggio con il quale Tolstoj stesso si pone di fronte alla realtà nella quale si trova immerso. Quest’uomo è Konstantin Dmitrevič Levin, l’alter ego di Tolstoj nel romanzo; ad egli infatti assegna l’importante ruolo di esporre quelle che realmente erano le sue personali posizioni riguardo a molte delle dinamiche sociali di quegli anni. Attraverso gli occhi di Levin viene infatti filtrata la trasformazione conseguente all’abolizione della servitù della gleba avvenuta nel 1861; questo era un tema che urgeva essere dibattuto (negli stessi anni ne parlano altri letterati illustri quali Turgenev e Nekrasov) in quanto veniva a crearsi un nuovo rapporto tra proprietario terriero e contadino che necessitava essere definito. Levin si interroga su questo nuovo rapporto e sembra propendere per la riduzione della produzione, il coinvolgimento dei lavoratori e la condivisione del loro lavoro, ciò che egli realmente si impegna a fare. Il vincolo, fino a questi anni indissolubile, che aveva legato il contadino alla terra si era protratto ingiustamente nel tempo per secoli, dall’epoca di Ivan III Velikij (1462-1505), lo costringeva a delle condizioni disumane di schiavitù che sempre più spesso venivano denunciate dagli intellettuali, paradigmatico è l’esempio delle Anime Morte (Mjortvyje duši) di Nikolaj Vasil’evič Gogol’, pubblicato nel 1842, senza nulla togliere al Viaggio da Pietroburgo a Mosca di Aleksandr Nikolaevič Radišev, pubblicato nel 1790. È ancora Levin che rappresenta la posizione di Tolstoj riguardo alla cosiddetta “questione slava”, sono infatti questi gli anni degli scrittori slavofili e della guerra serbo-turca, la quale Levin dichiara “una cosa bestiale, crudele e terribile” (cap. XV, OTTAVA PARTE), mentre in posizione opposta, dopo la morte di Anna, vediamo Vronskij che si arruola e parte per la guerra. L’argomento sul quale è imperniata tutta la vicenda è però la famiglia, non a caso il romanzo si apre proprio con la massima “tutte le famiglie felici sono simili, ma ogni famiglia infelice è infelice a modo suo”. L’istituzione del matrimonio viene immortalata nel momento della sua crisi, una crisi che Tolstoj sentiva molto vicina alla sua quotidianità, più volte, infatti, tentò di scappare via dalla sua famiglia, l’ultima delle quali la notte tra il 27 ed il 28 ottobre 1910; pochi giorni dopo morì. Levin è ancora una volta un personaggio chiave, in egli Tolstoj ripone tutte le sue speranze e spera che alla fine egli riesca a rispondere a quei quesiti che non davano pace a lui e continuavano a tormentarlo. Levin è un personaggio che medita e cerca, la sua coscienza non lo abbandona neanche un momento, durante tutto il romanzo egli si interroga sul mistero della vita e della morte, sul senso del mondo e dell’esistenza umana. Nella storia Levin è un vecchio amico di Stepan Arkad’evič Oblonskij (già citato marito di Dolly e fratello di Anna); nel cap. V della Parte Prima viene descritto il rapporto che lega i due: “Levin aveva quasi la stessa età di Oblonskij e si dava del ‘tu’ con lui non solo per lo champagne. Levin era stato suo compagno e amico della prima giovinezza. (…) Quando vedeva Levin, Oblonskij non riusciva a trattenere un leggero sorriso ironico. Erano già molte volte che l’aveva visto arrivare a Mosca dalla campagna, dove si occupava di qualcosa ma di cosa esattamente Stepan Arkad’evič non era mai riuscito a capirlo bene e la cosa non lo interessava. (…) Levin disprezzava in cuor suo lo stile mondano dell’amico, il suo lavoro, che derideva e considerava frivolo. Ma la differenza stava nel fatto che Oblonskij, facendo quello che facevano tutti, derideva con disinvolta benevolenza, mentre Levin era insicuro ed a volte astioso”. Fin da subito viene instaurato un contrasto tra la società rappresentata da Stepan e Levin, il quale ha un approccio tutto suo alla vita, sempre nuovo e sorprendente. Questo contrasto non è della stessa natura di quello di Anna, ma vede impegnato anche Levin, come Anna, alla ricerca di un proprio equilibrio nella vita, sentendosi entrambi lontani dalla realtà sociale nella quale vivevano. L’amicizia tra i due si pone come unico filo conduttore che collega l’eroina Anna all’alter ego di Tolstoj. Le loro storie si intrecceranno solo poche volte durante il romanzo, ma essi sono indissolubilmente collegati dalla stessa forza e dalla stessa tensione nel vivere la propria esistenza. Non è certo casuale che proprio l’integerrimo Levin, che tanto disprezzava e giudicava Anna per il suo commesso adulterio, rimanga stupito di fronte alla vita che prorompe dalla vista del quadro che in Italia il poeta Michajlov realizzò per Vronskij durante la loro idilliaca fuga d’amore: “Non era un quadro, ma un’incantevole donna viva con i neri capelli ondulati, le spalle e le braccia nude e un pensieroso accenno di sorriso sulle labbra (...) che lo guardava trionfalmente e teneramente con occhi che lo turbavano. Non era viva per il solo fatto che era più bella di quanto possa esserlo una donna viva”. A questi pensieri segue l’incontro con Anna che, di persona, agli occhi di Levin ha qualcosa di nuovo e affascinante che nel quadro non c’era, egli dunque, a differenza di Vronskij e di Michajlov, riesce a leggere in Anna quella bellezza interiore e la voglia di vivere che emana in quanto animo molto simile a quello dell’eroina. Levin trascorre una piacevolissima serata con Anna, discorrendo di letteratura, arte e pittura, e ne rimane completamente sedotto, tant’è che esclama entusiasta: “Sì, sì, ecco una donna!” e qui il paragone è spontaneo con Kity, sorella di Dolly e moglie di Levin. Ekaterina Aleksandrovna Ščerbatskaja è una donna sottomessa, non molto intelligente, le cui massime ambizioni sono fare la madre, la moglie e la padrona di casa. Sin dai primi tempi del loro matrimonio Levin si stupisce dell’assoluta inerzia di Kity, questi i suoi pensieri al riguardo: “oltre all’interesse per la casa (questo ce l’ha), oltre alla sua toilette e al ricamo inglese, non ha interessi seri. Non le interessa il mio lavoro, né l’azienda, né i contadini, né la musica, in cui è abbastanza forte, né la lettura. Non fa nulla ed è perfettamente soddisfatta”. Il suo itinerario nel romanzo, sebbene ella non si discosti molto dall’immagine tradizionale della donna, è abbastanza interessante da un punto di vista letterario. La osserviamo inizialmente come vittima collaterale di un amore non corrisposto per Vronskij, e ci ricorda da molti punti di vista Tat’jana Larina dell’Evgenij Onegin di Puškin, così come in seguito ella sarà la moglie buona e mite di Levin, completamente assorta nel suo ruolo, esattamente come la stessa citata Tat’jana; esse rappresentano in questo quadro l’immagine della donna che sperimenta l’amore vero solo tra le pagine dei libri, si illudono di poterlo trovare veramente ma sempre nella prospettiva di un futuro matrimonio; non si interrogano sulla sostanza delle cose, non guardano in profondità, non si interrogano, ma si adeguano, si plasmano alla mentalità del tempo, si conformano. Non è casuale che entrambe, dopo aver subìto una delusione d’amore, dopo un periodo di sofferenza trovino la pace proprio nel matrimonio. I libri degli esponenti del Sentimentalismo potevano tanto sull’educazione del cuore, ma nulla sull’istruzione della donna. Kity, dunque, risulta tanto conformista quanto la sorella Dolly, e lo dimostra il fatto che alla fine ella sposa Levin, sebbene inizialmente avesse rifiutato la sua proposta di matrimonio per inseguire il suo sogno amoroso per Vronskij. Levin, sposando Kity, il suo grande amore, pensa di trovare una miracolosa felicità coniugale che avrebbe posto fine a tutti i suoi dubbi ed incertezze sulla vita, cosa che realmente non accadde. Nonostante una relativa “felicità domestica” egli continua ad essere la vittima di un tormentato disagio esistenziale che si concreta in ciò che Tolstoj ci racconta di lui: “E, felice di padre di famiglia, uomo sano, Levin fu varie volte così vicino al suicidio, che nascose persino una corda per non impiccarsi e aveva paura di andare in giro con il fucile per la tentazione di spararsi.” Egli riesce a trovare, a differenza di Anna, un suo equilibrio che non deriva dalla realtà familiare, ma proprio dal fatto di non donarsi completamente alla moglie, di non confidare a lei le proprie riflessioni, anche perché non la trovava abbastanza intelligente da poterle comprendere, ma attraverso la continua preghiera: “(…) e continuerà ad esserci un muro tra il sacrario della mia anima e gli altri, compresa mia moglie, e continuerò ad accusare lei per le mie paure e pentirmene, continuerò a non comprendere con la ragione perché prego, e perché pregherò ancora (…)”. Pietro Citati, famoso scrittore e critico letterario nato nel 1930 a Firenze, nel suo libro intitolato Tolstoj, affermava che Anna Karenina è un romanzo sospeso nel vuoto, tra la vita e la morte e che il sentimento che permea tutta la storia è l’incertezza, che ha come unici spiragli di luce la fede in Dio di Levin e, paradossalmente, la morte di Anna; è un romanzo che non fornisce risposte, misterioso, come l’esistenza, in cui ogni lettore può trovare un pezzettino della propria anima. A mio parere, questo eloquente discorso di Citati esprime magnificamente e completamente quello che è il romanzo Anna Karenina, nel quale io personalmente intravvedo ad ogni lettura ed indagine più approfondita un pezzettino dell’anima del suo autore, Lev Nikolaevič Tolstoj. Anna e Levin, sono diametralmente opposti, ma entrambi sono alla ricerca di un vero senso della vita, tentando di fornire una risposta al suo principale quesito: il senso dell’esistenza umana. Questo mistero però agitò da sempre anche Tolstoj e fu il motivo principale della sua crisi spirituale, non poter rispondere alla fatidica domanda “perché?” che lo raggiungeva ed eclissava tutte le altre crea in lui l’arresto della vita ed una profonda angoscia inspiegabile. Quando questa crisi iniziò ad acuirsi Tolstoj aveva compiuto quarantanove anni e già prima di allora il pensiero della morte lo aveva già agitato prima riuscendo a rifugiarsi ora nella guerra (partecipò all’assedio di Sebastopoli durante la guerra di Crimea, combattutasi tra il 1853 ed il 1856, che aveva visto contrapposto alla Russia l’Impero Ottomano per il controllo dei Balcani e del Mediterraneo), ora nella letteratura, ora nella realizzazione della felicità familiare, ma quando in ultima istanza vide sciogliersi anche il suo piccolo sogno domestico, nel quale riponeva le sue ultime speranze, a causa dell’apatia, alla quale continuava ad essere assoggettato. I dubbi tormentosi che, alla fine del romanzo spingono Levin all’orlo del suicidio, sono i dubbi che risalgono Tolstoj, così come è sua la consapevolezza di Anna, che il matrimonio con Karenin rappresenta per lei la morte dei desideri, ella arriverà alla stessa deludente conclusione quando realizzerà che l’adulterio non è che una fase alternativa del matrimonio; il suo suicidio non è altro che l’espressione di questa consapevolezza. La preghiera di Levin è un atto di fede, ma non offre un senso alla vita; il suicidio di Anna è un atto di coraggio, ma con ciò la vita si dissolve e svanisce ogni possibilità che ella possa mai trovare la felicità o il senso della vita. La crisi è l’elemento chiave dell’opera di Tolstoj: la crisi della società, che da una parte tende verso l’evoluzione mentre dall’altra resta ancorata al passato, alla tradizione, alla superficialità; la crisi del matrimonio e attraverso questa la crisi della figura femminile, sempre tesa tra l’essere santa o peccatrice, madre o amante, onesta o perduta, in un dualismo inconciliabile; la crisi dell’individuo, Levin, alla costante ricerca del senso della vita; ed infine la crisi dell’uomo Tolstoj, che esibisce un sistema di concezioni del mondo in una prospettiva per la quale tutto è capovolto, egli mette in discussione tutto, si interroga, indaga, si interroga ancora, e poi affaticato e sfinito si rifugia nella fede in Dio, ricomincia laddove Levin aveva finito. ")])])])}]

